import React, { Suspense } from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';

import Layout from './components/layout/Layout';

import LoadingSpinner from './components/UI/LoadingSpinner';

const AddQuote = React.lazy(() => import('./pages/AddQuote'));
const QuoteDetails = React.lazy(() => import('./pages/QuoteDetails'));
const NotFound = React.lazy(() => import('./pages/NotFound'));
const Quotes = React.lazy(() => import('./pages/Quotes')); // user could possibly visit other non-main "page" first from incoming link

function App() {
  return (
    <Layout>
      <Suspense
        fallback={
          <div className='centered'>
            <LoadingSpinner />
          </div>
        }
      >
        <Switch>
          <Route path='/react-spa/' exact>
            <Redirect to='/react-spa/quotes' />
          </Route>
          <Route path='/react-spa/quotes' exact>
            <Quotes />
          </Route>
          <Route path='/react-spa/quotes/:quoteId'>
            <QuoteDetails />
          </Route>
          <Route path='/react-spa/add-quote'>
            <AddQuote />
          </Route>
          <Route path='*'>
            <NotFound />
          </Route>
        </Switch>
      </Suspense>
    </Layout>
  );
}

export default App;
